@import url('https://fonts.googleapis.com/css?family=Source+Sans+Pro:200,300');

.adminlogin,
.adminCheck {
  font-family: 'Source Sans Pro', sans-serif;
  color: white;
  font-weight: 300;
}

.adminlogin * {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-weight: 300;
}

.adminlogin {
  background: linear-gradient(to bottom right, #50a3a2 0%, #53e3a6 100%);
  position: absolute;
  top: 50%;
  left: 0;
  width: 100%;
  height: 400px;
  margin-top: -200px;
  overflow: hidden;
}

/* 2025.02.24  ,      UI */
.adminCheck {
  background: linear-gradient(to bottom right, #50a3a2 0%, #53e3a6 100%);
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  margin-top: 0;
  overflow-y: auto;
  scrollbar-width: none;

  .cameraBox{
    width: 100%;
    height: 480px;
    background-color: azure;
  }

  .admin-container{
    height: 100vh;
    min-height: 800px;
  }
}

.admin-container {
  max-width: 600px;
  margin: 0 auto;
  padding: 80px 0;
  height: 400px;
  text-align: center;
  position: relative;
  z-index: 2;
}

.admin-form {
  padding: 20px 0;
  position: relative;
  z-index: 2;
}

.admin-input-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 0px;
}

.admin-input {
  display: block;
  outline: 0;
  border: 1px solid rgba(255, 255, 255, 0.4);
  background-color: rgba(255, 255, 255, 0.2);
  width: 250px;
  border-radius: 3px;
  padding: 10px 15px;
  margin: 0 auto 5px auto;
  text-align: center;
  font-size: 18px;
  color: white;
  transition-duration: 0.25s;
  font-weight: 300;
}

.admin-input:hover {
  background-color: rgba(255, 255, 255, 0.4);
}

.admin-input:focus {
  background-color: white;
  width: 300px;
  color: #53e3a6;
}

.admin-button {
  outline: 0;
  background-color: white;
  border: 0;
  padding: 10px 15px;
  color: #53e3a6;
  border-radius: 3px;
  width: 250px;
  cursor: pointer;
  font-size: 18px;
  transition-duration: 0.25s;
  margin-top: 15px;
}

.admin-button:hover {
  background-color: rgb(245, 247, 249);
}

.admin-bg-bubbles {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
}

.admin-bg-bubbles li {
  position: absolute;
  list-style: none;
  display: block;
  width: 40px;
  height: 40px;
  background-color: rgba(255, 255, 255, 0.15);
  bottom: -160px;
  animation: square 25s infinite linear;
}

.admin-bg-bubbles li:nth-child(1) { left: 10%; }
.admin-bg-bubbles li:nth-child(2) { left: 20%; width: 80px; height: 80px; animation-delay: 2s; animation-duration: 17s; }
.admin-bg-bubbles li:nth-child(3) { left: 25%; animation-delay: 4s; }
.admin-bg-bubbles li:nth-child(4) { left: 40%; width: 60px; height: 60px; animation-duration: 22s; background-color: rgba(255, 255, 255, 0.25); }
.admin-bg-bubbles li:nth-child(5) { left: 70%; }
.admin-bg-bubbles li:nth-child(6) { left: 80%; width: 120px; height: 120px; animation-delay: 3s; background-color: rgba(255, 255, 255, 0.2); }
.admin-bg-bubbles li:nth-child(7) { left: 32%; width: 160px; height: 160px; animation-delay: 7s; }
.admin-bg-bubbles li:nth-child(8) { left: 55%; width: 20px; height: 20px; animation-delay: 15s; animation-duration: 40s; }
.admin-bg-bubbles li:nth-child(9) { left: 25%; width: 10px; height: 10px; animation-delay: 2s; animation-duration: 40s; background-color: rgba(255, 255, 255, 0.3); }
.admin-bg-bubbles li:nth-child(10) { left: 90%; width: 160px; height: 160px; animation-delay: 11s; }

@keyframes square {
  0% { transform: translateY(0); }
  100% { transform: translateY(-700px) rotate(600deg); }
}
