.btn-postion{
    position: absolute;
    right: 15px;
    top:-50px;
    @media only screen and (max-width: $break-xsmall) {
        position: unset;
        right: unset;
        top:unset;
    }
}
.no-bg{
    background-color: transparent !important;
}
.pointer{
    cursor: pointer;
}
.chooser { position: absolute; z-index: 1; opacity: 0; cursor: pointer; width: 100%;}
.dividers {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
    align-items: center;
    &:after,
    &:before{
        -ms-flex: 1 1 0%;
        flex: 1 1 0%;
        border-top: .0625rem solid var(--border-color);
        content: "";
        margin-top: .0625rem;
    }
    &::before{
        margin-right: 1.5rem;
    }
    &::after{
        margin-left: 1.5rem;
    }
}
.dividers-block{
    margin: 1rem 0;
    color: inherit;
    background-color: currentColor;
    border: 0;
    opacity: .25;
    &:not([size]) {
        height: 1px;
    }
}
.task_status{
    span{
        width: 15px;
        height: 15px;
        display: block;
    }
}
[data-theme="dark"] {
    .shadow-sm{
        box-shadow: 0 0.125rem 0.25rem rgba($white, 0.15) !important;
    }
    .shadow{
        box-shadow: 0 0.5rem 1rem rgba($white, 0.15) !important;
    }
    .text-muted{
        color: #5b5b5b !important;
    }
    .table{
        .table-dark{
            --bs-table-bg: var(--primary-color);
            tr{
                th{
                    color: var(--text-color);
                    background-color: #313131;
                }
            }
        }
    }
    .light-warning-bg {
        background-color: #ead09d !important;
    }
    .big-search{
        color: var(--text-color);
        &:focus{
            color: var(--text-color);
        }
    }
    .timeline-item {
        border-left: 2px solid var(--border-color) !important;
        .avatar{
            color: var(--border-color);
        }
    }
    .border, .border-left, .border-top, .border-right, .border-bottom{
        border-color:var(--border-color) !important;
    }
    .table-hover>tbody>tr:hover{
        --bs-table-accent-bg: var(--border-color);
    }
    .bg-lightyellow,.bg-lightblue,.bg-lightgreen,.light-warning-bg,.light-primary-bg,.light-info-bg,
    .light-danger-bg,.light-success-bg,.light-orange-bg,.bg-info-light,.bg-santa-fe,.bg-careys-pink,.bg-lavender-purple{
        color: var(--body-color);
    }
    .top-perfomer{
        h6{
            color: var(--text-color);
        }
    }

}