            }
        }
    }
    
}
.todo_list{
    li{
        margin-bottom: 10px;
        padding-bottom: 10px;
        border-bottom: 1px solid var(--border-color);
        &:last-child{
            border-bottom: 0;
        }
        label{
            position: relative;
            &:before{
                position: absolute;
                content: "";
                background-color: var(--secondary-color);
                width: 25px;
                height: 25px;
                left:0px;
                top:0px;
                z-index: 1;
                border-radius: .25rem;
            }
            input{
                position: relative;
                z-index: 9;
                font-size: 16px;
                margin: 5px;
                &.form-check-input{
                    background-color: rgba(var(--secondary-color),0.5);
                    border-color: var(--secondary-color);
                    box-shadow: none;
                }
            }
            span{
                margin-left: 10px;
            }
        }
    }
    .custom-checkbox{
        input[type="checkbox"]{
            &:checked + span{
                text-decoration: line-through;
            }
        }
    }
}
.brand-icon {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    .logo-icon{
        width: 60px;
        height: 60px;
        min-width: 60px;
        min-height: 60px;
        border-radius: 50%;
        background-color: var(--white-color);
        margin: inherit;
        line-height: 60px;
        display: flex;
        align-items: center;
        justify-content: center;
        svg{
            width: 50px;
            path{
                fill: var(--primary-color);
            }
        }
    }
    .logo-text{
        font-weight: bold;
        color: var(--white-color);
        font-size: 18px;
        display: inline-block;
        padding-left: 15px;
    }
}
.pulse {
    .pulse-ring {
        border-color: var(--primary-color);
        display: block;
        position: absolute;
        -webkit-animation: animation-pulse 3.5s ease-out;
        animation: animation-pulse 3.5s ease-out;
        -webkit-animation-iteration-count: infinite;
        animation-iteration-count: infinite;
        border-style: solid;
        top: 0;
        left: 3px;
        border-radius: 45px;
        height: 45px;
        width: 45px;
        opacity: 0;
        border-width: 5px;
    }

    &::after{
        display: none;
    }

    @-webkit-keyframes animation-pulse {
        0% {
            -webkit-transform: scale(0.1, 0.1);
            opacity: 0.0; 
        }
        60% {
            -webkit-transform: scale(0.1, 0.1);
            opacity: 0.0;
        }
        65% {
            opacity: 1;
        }
        100% {
            -webkit-transform: scale(1.2, 1.2);
            opacity: 0.0;
        }
    }
      
    @keyframes animation-pulse {
        0% {
            -webkit-transform: scale(0.1, 0.1);
            opacity: 0.0;
        }
        60% {
            -webkit-transform: scale(0.1, 0.1);
            opacity: 0.0;
        }
        65% {
            opacity: 1;
        }
        100% {
            -webkit-transform: scale(1.2, 1.2);
            opacity: 0.0;
        }
    }
}
