// header notifications 
.notifications {
    .pulse,
    .pulse:focus{
        color: var(--text-color) !important;
    }
    .card{
        .card-header{
            background-color: var(--primary-color);
            color: $white;

            .nav-tabs{
                
                .nav-link{
                    color: rgba($white, .2);
                    font-size: 1rem;
                    border: 0;

                    &.active,
                    &:hover{
                        color: var(--secondary-color);
                        background-color: transparent;

                        &::after{
                            content: '';
                            width: 7px;
                            height: 7px;
                            background-color: var(--secondary-color);
                            display: block;
                            border-radius: 50%;
                            margin: 5px auto 0;
                        }
                    }
                }
            }
        }
        .list{
            @include overflow(scroll);
            height: 330px;
            overflow-y: auto;
        }
    }
    .dropdown-menu{

        @media only screen and (max-width:  $break-small - 1px) {
            width: 280px;
        }
        @media only screen and (max-width:  320px) {
          width: 220px;
        }
        .w380{
            @media only screen and (max-width:  $break-small - 1px) {
                width: 280px;
            }
            @media only screen and (max-width:  320px) {
              width: 220px;
            }
        }
    }
}

