}
.iconlist {
    margin: 0;
    padding: 0;
    list-style: none;
    text-align: center;
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    .icon-holder {
        position: relative;
        text-align: center;
        border-radius: 3px;
        overflow: hidden;
        padding-bottom: 5px;
        border: 1px solid var(--border-color);
        transition: all 0.2s linear 0s;
        &:hover {
            background: var(--secondary-color);
            color: #ffffff;
            .icon i {
                color: #ffffff;
            }
        }
        .icon {
            padding: 20px;
            text-align: center;
            i {
                font-size: 3em;
                color: var(--font-color)
            }
        }
        span {
            font-size: 14px;
            display: block;
            margin-top: 5px;
            border-radius: 3px;
        }
    }
 }
